import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	data() {
		return {
			shopList: [{
				name: '工厂',
				value: 0,
				imgSrc: '../../../static/img/mine/checked-false.png'
			}, {
				name: '商家',
				value: 1,
				imgSrc: '../../../static/img/mine/checked-false.png'
			}, {
				name: '餐饮',
				value: 2,
				imgSrc: '../../../static/img/mine/checked-false.png'
			}],
			current: -1,
			disabled: true,
			maskFlag: false
		};
	},
	components: {
		uniPopup
	},
	methods: {
		checkShopType(e) {
			this.current = e.detail.value
			for (let i = 0; i < this.shopList.length; i++) {
				if (this.current == i) {
					this.shopList[i].imgSrc = '../../../static/img/mine/checked-true.png'
				} else {
					this.shopList[i].imgSrc = '../../../static/img/mine/checked-false.png'
				}
			}
			this.disabled = false
		},
		close() {
			this.$refs['remaks'].close()
		},
		nextStep() {
			console.log('我点击了下一步')
			if (this.current == 0) {
				uni.navigateTo({
					url: '/pages/mine/openShop/factoryRule'
				})
			} else if (this.current == 1) {
				uni.navigateTo({
					url: '/pages/mine/openShop/shopRule'
				})
			} else if (this.current == 2) {
				console.log('我选择的是餐饮')
				this.$refs['remaks'].open()
			} else {

			}
		},
		toAnother() {
			this.$refs['remaks'].close()

			uni.showToast({
				title: '餐饮门店管理即将上线，敬请期待',
				duration: 1000,
				icon: 'none'
			})


		}
	}
}
